,CACxB,AACD,mBACE,+BAAkC,CACnC,AACD,oCACE,yBAA0B,AAC1B,2BAA4B,AAC5B,aAAc,AACd,iBAAmB,CACpB,AACD,0BACE,+BAAiC,CAClC,AACD,qDACE,gBAAiB,AACjB,wBAA0B,AAC1B,+BAAiC,CAClC,AACD,4BACE,aAAe,CAChB,AACD,kCACE,eAAgB,AAChB,YAAc,CACf,AACD,qBACE,iBAAmB,CACpB,AACD,uBACE,eAAgB,AAChB,wBAA0B,AAC1B,mBAAqB,CACtB,AACD,0BACE,cAAe,AACf,gBAAiB,AACjB,iBAAkB,AAClB,+BAAiC,CAClC,AACD,qCACE,kBAAoB,CACrB,AACD,4BACE,gBAAiB,AACjB,mBAAoB,AACpB,sBAAwB,CACzB,AACD,4BACE,cAAe,AACf,YAAa,AACb,iBAAkB,AAClB,kBAAmB,AACnB,eAAgB,AAChB,UAAY,CACb,AACD,iCACE,yDAA0D,CAC3D,AACD,iEACE,yDAAyD,CAC1D,AACD,iEACE,yDAAyD,CAC1D,AACD,gCACE,yDAAyD,CAC1D,AACD,gCACE,yDAAyD,CAC1D,AACD,gCACE,yDAAyD,CAC1D,AACD,gCACE,yDAAgE,CACjE,AACD,iEACE,yBAA2B,CAC5B,AACD,mCACE,wBAA0B,CAC3B,AACD,+BACE,eAAgB,AAChB,UAAY,CACb,AACD,mBACE,aAAe,CAChB,AACD,mBACE,WAAa,CACd","file":"index.vue","sourcesContent":["\n@charset \"UTF-8\";\n/*\r\n * mescroll -- 精致的下拉刷新和上拉加载js框架  ( a great JS framework for pull-refresh and pull-up-loading )\r\n * version 1.1.6\r\n * 2017-08-27\r\n * https://github.com/mescroll/mescroll.git\r\n * http://www.mescroll.com\r\n * author: wenju < mescroll@qq.com > 文举\r\n * \r\n * ----- mescroll的html结构解析 ----\r\n * \r\n   <div id=\"mescroll\" class=\"mescroll\">\r\n\t\r\n\t\t//下拉刷新区域 ( mescroll初始化之后自动创建 )\r\n\t\t<div class=\"mescroll-downwarp\">\r\n\t\t\t<div class=\"downwarp-content\">\r\n\t\t\t\t<p class=\"downwarp-progress\"></p> <p class=\"downwarp-tip\">下拉刷新 </p>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t\t\r\n\t\t//界面的具体内容\r\n\t\t//<div>界面内容</div>\r\n\t\t//数据列表..\r\n\t\t//<ul id=\"dataList\" class=\"data-list\">\r\n\t\t//\t<li>数据列表</li>\r\n\t\t\r\n\t\t\t//空布局 ( 列表无任何数据时, 自动创建显示 )\r\n\t\t\t<div class=\"mescroll-empty\">\r\n\t\t\t\t<img class=\"empty-icon\" src=\"../img/mescroll-empty.png\"/>\r\n\t\t\t\t<p class=\"empty-tip\">暂无相关数据~</p>\r\n\t\t\t\t <p class=\"empty-btn\">去逛逛 ></p> \r\n\t\t\t</div>\r\n\t\t\t\r\n\t\t//</ul>\r\n\t\r\n\t\t//上拉加载区域 ( mescroll初始化之后自动创建 )\r\n\t\t<div class=\"mescroll-upwarp\">\r\n\t\t\t//加载中..\r\n\t\t\t<p class=\"upwarp-progress mescroll-rotate\"></p><p class=\"upwarp-tip\">加载中..</p>\r\n\t\t\t//无数据\r\n\t\t\t<p class=\"upwarp-nodata\">-- END --</p>\r\n\t\t</div>\r\n\t\t\r\n\t\t//回到顶部按钮 ( 列表滚动到配置的距离时, 自动创建显示 )\r\n\t\t<img class=\"mescroll-totop\" src=\"../img/mescroll-totop.png\"/>\r\n\t\t\r\n\t</div>\r\n * \r\n */\n/*下拉刷新和上拉加载的滑动区域*/\n.mescroll {\n  width: 100%;\n  height: 100%;\n  overflow-y: auto;\n  -webkit-overflow-scrolling: touch;\n}\n\n/*启用硬件加速:动画渲染流畅,解决IOS下拉刷新因隐藏进度条致使的闪屏问题,在下拉刷新和上拉加载触发时启用,结束后移除,避免滥用导致其他兼容性问题*/\n.mescroll-hardware {\n  -webkit-transform: translateZ(0);\n  transform: translateZ(0);\n}\n\n/*下拉刷新区域*/\n.mescroll-downwarp {\n  position: relative;\n  width: 100%;\n  height: 0;\n  overflow: hidden;\n  text-align: center;\n}\n\n/*下拉刷新--高度重置的过渡动画*/\n.mescroll-downwarp-reset {\n  -webkit-transition: height 300ms;\n  transition: height 300ms;\n}\n\n/*下拉刷新--内容区,定位于区域底部*/\n.mescroll-downwarp .downwarp-content {\n  position: absolute;\n  left: 0;\n  bottom: 0;\n  width: 100%;\n  min-height: 30px;\n  padding: 10px 0;\n}\n\n/*上拉加载区域*/\n.mescroll-upwarp {\n  min-height: 30px;\n  padding: 15px 0;\n  text-align: center;\n  visibility: hidden;\n  /*代替display: none,列表快速滑动到底部能及时显示上拉加载的区域*/\n}\n\n/*下拉刷新,上拉加载--提示文本*/\n.mescroll-downwarp .downwarp-tip,\n.mescroll-upwarp .upwarp-tip,\n.mescroll-upwarp .upwarp-nodata {\n  display: inline-block;\n  font-size: 12px;\n  color: gray;\n  vertical-align: middle;\n}\n\n/*下拉刷新,上拉加载--旋转进度条*/\n.mescroll-downwarp .downwarp-progress,\n.mescroll-upwarp .upwarp-progress {\n  display: inline-block;\n  width: 16px;\n  height: 16px;\n  border-radius: 50%;\n  border: 1px solid gray;\n  margin-right: 8px;\n  border-bottom-color: transparent;\n  vertical-align: middle;\n}\n\n/*旋转动画*/\n.mescroll-rotate {\n  -webkit-animation: mescrollRotate 0.6s linear infinite;\n  animation: mescrollRotate 0.6s linear infinite;\n}\n@-webkit-keyframes mescrollRotate {\n0% {\n    -webkit-transform: rotate(0deg);\n}\n100% {\n    -webkit-transform: rotate(360deg);\n}\n}\n@keyframes mescrollRotate {\n0% {\n    transform: rotate(0deg);\n}\n100% {\n    transform: rotate(360deg);\n}\n}\n\n/*无任何数据的空布局*/\n.mescroll-empty {\n  width: 100%;\n  padding-top: 20px;\n  text-align: center;\n}\n.mescroll-empty .empty-icon {\n  width: 90px;\n  height: 90px;\n}\n.mescroll-empty .empty-tip {\n  margin-top: 6px;\n  font-size: 14px;\n  color: gray;\n}\n.mescroll-empty .empty-btn {\n  max-width: 50%;\n  margin: 20px auto;\n  padding: 10px;\n  border: 1px solid #65AADD;\n  border-radius: 6px;\n  background-color: white;\n  color: #65AADD;\n}\n.mescroll-empty .empty-btn:active {\n  opacity: .75;\n}\n\n/*回到顶部的按钮*/\n.mescroll-totop {\n  z-index: 9990;\n  position: fixed;\n  right: 10px;\n  bottom: 30px;\n  width: 36px;\n  height: 36px;\n  border-radius: 50%;\n  opacity: 0;\n}\n\n/*显示动画--淡入*/\n.mescroll-fade-in {\n  -webkit-animation: mescrollFadeIn .5s linear forwards;\n  animation: mescrollFadeIn .5s linear forwards;\n}\n@-webkit-keyframes mescrollFadeIn {\n0% {\n    opacity: 0;\n}\n100% {\n    opacity: 1;\n}\n}\n@keyframes mescrollFadeIn {\n0% {\n    opacity: 0;\n}\n100% {\n    opacity: 1;\n}\n}\n\n/*隐藏动画--淡出*/\n.mescroll-fade-out {\n  pointer-events: none;\n  -webkit-animation: mescrollFadeOut .5s linear forwards;\n  animation: mescrollFadeOut .5s linear forwards;\n}\n@-webkit-keyframes mescrollFadeOut {\n0% {\n    opacity: 1;\n}\n100% {\n    opacity: 0;\n}\n}\n@keyframes mescrollFadeOut {\n0% {\n    opacity: 1;\n}\n100% {\n    opacity: 0;\n}\n}\n\n/*滚动条轨道背景(默认在PC端设置)*/\n.mescroll-bar::-webkit-scrollbar-track {\n  background-color: transparent;\n}\n\n/*滚动条轨道宽度 (默认在PC端设置)*/\n.mescroll-bar::-webkit-scrollbar {\n  width: 6px;\n}\n\n/*滚动条游标 (默认在PC端设置)*/\n.mescroll-bar::-webkit-scrollbar-thumb {\n  border-radius: 6px;\n  background-color: #ccc;\n}\n\n/*滚动条游标鼠标经过的颜色变化 (默认在PC端设置)*/\n.mescroll-bar::-webkit-scrollbar-thumb:hover {\n  background-color: #aaa;\n}\n.doc-message input {\n  border: 1px solid #ddd !important;\n}\n.doc-message .jc-search input:focus {\n  border: 1px solid #B9D3EE;\n  box-shadow: 0 0 5px #B9D3EE;\n  outline: none;\n  border-radius: 4px;\n}\n.doc-message .mint-navbar {\n  border-bottom: 1px solid #e2e2e2;\n}\n.doc-message .mint-navbar .mint-tab-item.is-selected {\n  margin-bottom: 0;\n  color: #4d8afe !important;\n  border-bottom: 3px solid #4d8afe;\n}\n.doc-message .mint-tab-item {\n  color: #6a707e;\n}\n.doc-message .mint-tab-item-label {\n  font-size: 16px;\n  margin: 0 5px;\n}\n.doc-message .scroll {\n  overflow-y: scroll;\n}\n.doc-message .doc-list {\n  padding: 0 15px;\n  display: block !important;\n  padding-bottom: 30px;\n}\n.doc-message .doc-list li {\n  display: block;\n  min-height: 65px;\n  line-height: 24px;\n  border-bottom: 1px solid #ededed;\n}\n.doc-message .doc-list li:last-child {\n  border-bottom: none;\n}\n.doc-message .doc-list li p {\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n}\n.doc-message .doc-list li a {\n  display: block;\n  height: 55px;\n  padding-top: 10px;\n  padding-left: 50px;\n  font-size: 17px;\n  color: #333;\n}\n.doc-message .doc-list li a.file {\n  background: url(\"../images/08-else.png\") 0 15px no-repeat;\n}\n.doc-message .doc-list li a.doc, .doc-message .doc-list li a.docx {\n  background: url(\"../images/01-doc.png\") 0 15px no-repeat;\n}\n.doc-message .doc-list li a.xls, .doc-message .doc-list li a.xlsx {\n  background: url(\"../images/03-xls.png\") 0 15px no-repeat;\n}\n.doc-message .doc-list li a.ppt {\n  background: url(\"../images/02-ppt.png\") 0 15px no-repeat;\n}\n.doc-message .doc-list li a.pdf {\n  background: url(\"../images/05-pdf.png\") 0 15px no-repeat;\n}\n.doc-message .doc-list li a.txt {\n  background: url(\"../images/07-txt.png\") 0 15px no-repeat;\n}\n.doc-message .doc-list li a.dir {\n  background: url(\"../images/06-wenjianjia.png\") 0 15px no-repeat;\n}\n.doc-message .doc-list li a.file, .doc-message .doc-list li a.dir {\n  background-size: 35px 40px;\n}\n.doc-message .doc-list li a:active {\n  background-color: #e6e6e6;\n}\n.doc-message .doc-list li span {\n  font-size: 14px;\n  color: #999;\n}\n.doc-message .time {\n  margin: 0 10px;\n}\n.doc-message .bulk {\n  float: right;\n}\n"],"sourceRoot":""}]);

